.parte-abajo{
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: #DBDFFD;
    color: #9BA3EB;
    width: 100%;
    height: 45px;
    line-height: 30px;
    padding: 5px 27px;
}

@media screen and (min-width:1024px) {

.parte-abajo{
        display: flex;
        justify-content: space-between;
        height: 40px;
    }    
}